import React from "react";
import { useHistory } from "react-router-dom";
import Slider from "react-slick";
import useScrollReveal from "#shared/hooks/useScrollReveal";
import WithLayersButton from "#shared/ui/WithLayersButton";
import { ROUTE_PATHS } from "#screens/route-paths";
import {
    Wrapper,
    TopTitle,
    TitleWrapper,
    BottomDescription,
    BlogListWrapper,
    BlogListContainer,
    BlogSliderContainer,
    BlogSlideWrapper,
    BlogSlide,
} from "./styled";
import BlogPreview from "./BlogPreview";
import { latestFromBlogData } from "./data";

const LatestFromBlog = () => {
    const ref = React.useRef();
    const { hasRevealed } = useScrollReveal({ ref });
    const runAnimation = hasRevealed ? "runAnimation" : "";
    const history = useHistory();

    const gotoBlog = () => {
        history.push(ROUTE_PATHS.blog);
    };

    return (
        <Wrapper ref={ref}>
            <TitleWrapper>
                <TopTitle className={runAnimation}>Latest from the blog</TopTitle>
                <BottomDescription className={runAnimation}>
                    Understand the latest on wine investing, industry trends and wines to watch on our blog.
                </BottomDescription>
                <WithLayersButton
                    colors={["#fff", "rgb(36, 46, 53)", "rgb(36, 46, 53)"]}
                    width="165.2px"
                    onClick={gotoBlog}
                >
                    READ MORE
                </WithLayersButton>
            </TitleWrapper>
            <BlogListContainer>
                <BlogListWrapper>
                    {latestFromBlogData.map(({ id, ...props }) => (
                        <BlogPreview key={id} {...props} id={id} runAnimation={runAnimation} delay={id + 1} />
                    ))}
                </BlogListWrapper>
            </BlogListContainer>
            <BlogSliderContainer>
                <Slider
                    className="blog-slider-container"
                    accessibility
                    infinite
                    autoplay={false}
                    slidesToShow={1}
                    slidesToScroll={1}
                    arrows={false}
                    dots
                >
                    {latestFromBlogData.map(({ id, ...props }) => (
                        <BlogSlide key={id}>
                            <BlogSlideWrapper>
                                <BlogPreview {...props} id={id} />
                            </BlogSlideWrapper>
                        </BlogSlide>
                    ))}
                </Slider>
            </BlogSliderContainer>
        </Wrapper>
    );
};

export default LatestFromBlog;
